<template>
	<div>
		<ImgButton v-model="imgurl" :multiple="true" @change="changeImgs"></ImgButton>
		<ImgButton v-model="imgurl2" @change="changeImg"></ImgButton>
		<img :src="imgurl2" width="100" height="100" />
		<template v-for="item in imgurl">
			<img :src="item" width="100" height="100"  />
		</template>
	</div>
</template>

<script>
	import ImgButton from "@/components/ImgButton"
	export default {
		name: "Login",
	    components: {
	    	ImgButton
	    },
	    props: {},
	    data() {
	      return {
	      	imgurl: '',
	      	imgurl2: ''
	      };
	    },
	    watch: {},
	    computed: {},
	    methods: {
	    	changeImgs: function(){
	    		console.log("选择多张图片" + this.imgurl)
	    	},
	    	changeImg: function(){
	    		console.log("选择图片" + this.imgurl)
	    	}
	    },
	    created() {
	    },
	    mounted() {
	    }
	};
</script>

<style>
</style>


